import React, { useState, useEffect } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import { getProblem, submitCode } from '../services/api';
import './ProblemDetailPage.css';

const ProblemDetailPage = () => {
  const { problemId } = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  const [problem, setProblem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [submission, setSubmission] = useState(null);
  const [isTestMode, setIsTestMode] = useState(false);
  
  // 
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [options, setOptions] = useState([]);
  
  // 
  const [judgeAnswer, setJudgeAnswer] = useState('');
  
  // 
  const [shortAnswer, setShortAnswer] = useState('');
  
  // 
  const [form, setForm] = useState({
    code: '',
    language: 'python'
  });

  useEffect(() => {
    // 
    if (location.state && location.state.problem) {
      setProblem(location.state.problem);
      setIsTestMode(true);
      setLoading(false);
      
      // 
      if (location.state.problem.type === 'choice') {
        initializeChoiceOptions(location.state.problem);
      }
    } else {
      fetchProblem();
    }
  }, [problemId, location.state]);

  const fetchProblem = async () => {
    try {
      setLoading(true);
      const result = await getProblem(problemId);
      setProblem(result);
      
      // 
      if (result.type === 'choice') {
        initializeChoiceOptions(result);
      }
    } catch (error) {
      setError('');
      console.error('Error fetching problem:', error);
    } finally {
      setLoading(false);
    }
  };

  // 
  const initializeChoiceOptions = (problemData) => {
    console.log('===  ===');
    console.log(':', problemData);
    console.log('choice_options :', problemData.choice_options);
    console.log('choice_options :', typeof problemData.choice_options);
    console.log('choice_options :', problemData.choice_options ? problemData.choice_options.length : 'null');
    
    // 
    let optionsData = null;
    
    // 1:  choice_options 
    if (problemData.choice_options) {
      optionsData = problemData.choice_options;
      console.log('1:  choice_options ');
    }
    
    // 2:  test_cases 
    if (!optionsData && problemData.test_cases) {
      optionsData = problemData.test_cases;
      console.log('2:  test_cases ');
    }
    
    // 3:  options 
    if (!optionsData && problemData.options) {
      optionsData = problemData.options;
      console.log('3:  options ');
    }
    
    // 4:  choices 
    if (!optionsData && problemData.choices) {
      optionsData = problemData.choices;
      console.log('4:  choices ');
    }
    
    // 5:  answer_options 
    if (!optionsData && problemData.answer_options) {
      optionsData = problemData.answer_options;
      console.log('5:  answer_options ');
    }
    
    if (optionsData) {
      // optionsData
      if (typeof optionsData === 'string') {
        const optionsStr = optionsData.trim();
        console.log(':', optionsStr);
        
        if (optionsStr.length === 0) {
          console.log('');
          setOptions([]);
          return;
        }
        
        try {
          // JSON
          const parsedOptions = JSON.parse(optionsStr);
          console.log('JSON:', parsedOptions);
          if (Array.isArray(parsedOptions) && parsedOptions.length > 0) {
            setOptions(parsedOptions);
            return;
          } else {
            console.log('JSON');
          }
        } catch (e) {
          console.log('JSON:', e.message);
        }
        
        // JSON
        console.log('...');
        const lines = optionsStr
          .split('\n')
          .map(line => line.trim())
          .filter(line => line.length > 0);
        
        console.log(':', lines);
        console.log(':', lines.length);
        
        if (lines.length > 0) {
          const parsedOptions = lines.map((line, index) => {
            //  "A.", "B." 
            const cleanText = line.replace(/^[A-Z]\.\s*/, '').trim();
            return {
              id: String.fromCharCode(65 + index), // A, B, C, D...
              text: cleanText,
              value: cleanText
            };
          });
          
          console.log(':', parsedOptions);
          setOptions(parsedOptions);
          return;
        } else {
          console.log('');
        }
      } else if (Array.isArray(optionsData)) {
        // optionsData
        console.log(':', optionsData);
        setOptions(optionsData);
        return;
      } else {
        console.log(':', optionsData);
      }
    } else {
      console.log('');
    }
    
    // 
    console.log('');
    setOptions([]);
  };

  // 
  const handleOptionChange = (optionId) => {
    if (problem.is_multiple_choice) {
      // 
      setSelectedOptions(prev => {
        if (prev.includes(optionId)) {
          return prev.filter(id => id !== optionId);
        } else {
          return [...prev, optionId];
        }
      });
    } else {
      // 
      setSelectedOptions([optionId]);
    }
    
    // 
    const optionElements = document.querySelectorAll('.option-item');
    optionElements.forEach(element => {
      const input = element.querySelector('input');
      if (input.checked) {
        element.classList.add('selected');
      } else {
        element.classList.remove('selected');
      }
    });
  };

  // 
  const handleJudgeAnswerChange = (answer) => {
    setJudgeAnswer(answer);
  };

  // 
  const handleShortAnswerChange = (e) => {
    setShortAnswer(e.target.value);
  };

  // 
  const handleCodeChange = (e) => {
    setForm({...form, code: e.target.value});
  };

  // 
  const handleLanguageChange = (e) => {
    setForm({...form, language: e.target.value});
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    let submissionData = {
      problem_id: parseInt(problemId),
      language: 'text'
    };

    // 
    switch (problem.type) {
      case 'choice':
        if (selectedOptions.length === 0) {
          setError('');
          return;
        }
        submissionData.code = selectedOptions.join(',');
        submissionData.language = 'choice';
        break;
        
      case 'judge':
        if (!judgeAnswer) {
          setError('');
          return;
        }
        submissionData.code = judgeAnswer;
        submissionData.language = 'judge';
        break;
        
      case 'short_answer':
        if (!shortAnswer.trim()) {
          setError('');
          return;
        }
        submissionData.code = shortAnswer;
        submissionData.language = 'short_answer';
        break;
        
      case 'programming':
        if (!form.code.trim()) {
          setError('');
          return;
        }
        submissionData.code = form.code;
        submissionData.language = form.language;
        break;
        
      default:
        setError('');
        return;
    }

    setSubmitting(true);
    setError('');
    
    try {
      const result = await submitCode(submissionData);
      setSubmission(result);
      // 
      navigate(`/result/${result.submission_id}`);
    } catch (error) {
      setError(error.response?.data?.error || '');
    } finally {
      setSubmitting(false);
    }
  };

  const getDifficultyColor = (diff) => {
    switch (diff) {
      case 'easy': return '#52c41a';
      case 'medium': return '#faad14';
      case 'hard': return '#f5222d';
      default: return '#666';
    }
  };

  const getDifficultyText = (diff) => {
    switch (diff) {
      case 'easy': return '';
      case 'medium': return '';
      case 'hard': return '';
      default: return '';
    }
  };

  const getTypeText = (type) => {
    switch (type) {
      case 'programming': return '';
      case 'choice': return '';
      case 'judge': return '';
      case 'short_answer': return '';
      default: return '';
    }
  };

  if (loading) {
    return (
      <div className="problem-detail-container">
        <div className="loading">...</div>
      </div>
    );
  }

  if (error || !problem) {
    return (
      <div className="problem-detail-container">
        <div className="error-message">{error || ''}</div>
      </div>
    );
  }

  return (
    <div className="problem-detail-container">
      {/*  */}
      {isTestMode && (
        <div className="test-mode-header">
          <button 
            onClick={() => navigate('/test')} 
            className="back-to-test-btn"
          >
             
          </button>
          <div className="test-mode-badge"></div>
        </div>
      )}

      <div className="problem-header">
        <div className="problem-title-section">
          <h1>{problem.id}. {problem.title}</h1>
          <div className="problem-badges">
            <span 
              className="type-badge"
              style={{ backgroundColor: '#1890ff' }}
            >
              {getTypeText(problem.type)}
            </span>
            <span 
              className="difficulty-badge"
              style={{ backgroundColor: getDifficultyColor(problem.difficulty) }}
            >
              {getDifficultyText(problem.difficulty)}
            </span>
          </div>
        </div>
        
        {problem.type === 'programming' && (
          <div className="problem-meta">
            <div className="meta-item">
              <span className="meta-label">:</span>
              <span className="meta-value">{problem.time_limit}ms</span>
            </div>
            <div className="meta-item">
              <span className="meta-label">:</span>
              <span className="meta-value">{problem.memory_limit}MB</span>
            </div>
          </div>
        )}
      </div>

      <div className="problem-content">
        <div className="problem-description">
          <h2></h2>
          <div className="description-text">
            {problem.description.split('\n').map((line, index) => (
              <p key={index}>{line}</p>
            ))}
          </div>
        </div>

        {/*  */}
        {problem.type === 'programming' && (
          <div className="problem-examples">
            <h2></h2>
            {problem.test_cases && (
              <div className="example-section">
                <h3>:</h3>
                <pre className="example-code">{problem.test_cases}</pre>
              </div>
            )}
            {problem.expected_output && (
              <div className="example-section">
                <h3>:</h3>
                <pre className="example-code">{problem.expected_output}</pre>
              </div>
            )}
          </div>
        )}

        {problem.type === 'choice' && (
          <div className="problem-options">
            <h2></h2>
            <div className="options-list">
              {options.length > 0 ? (
                options.map((option) => (
                  <label key={option.id} className="option-item">
                    <input
                      type={problem.is_multiple_choice ? "checkbox" : "radio"}
                      name="choice"
                      value={option.id}
                      checked={selectedOptions.includes(option.id)}
                      onChange={() => handleOptionChange(option.id)}
                    />
                    <span className="option-text">
                      <strong>{option.id}.</strong> {option.text}
                    </span>
                  </label>
                ))
              ) : (
                <div className="no-options">
                  <p></p>
                </div>
              )}
            </div>
            {problem.is_multiple_choice && (
              <p className="multiple-choice-hint"></p>
            )}
          </div>
        )}

        {problem.type === 'judge' && (
          <div className="problem-judge">
            <h2></h2>
            <div className="judge-options">
              <label className="judge-option">
                <input
                  type="radio"
                  name="judge"
                  value="A"
                  checked={judgeAnswer === "A"}
                  onChange={() => handleJudgeAnswerChange("A")}
                />
                <span className="judge-text"></span>
              </label>
              <label className="judge-option">
                <input
                  type="radio"
                  name="judge"
                  value="B"
                  checked={judgeAnswer === "B"}
                  onChange={() => handleJudgeAnswerChange("B")}
                />
                <span className="judge-text"></span>
              </label>
            </div>
          </div>
        )}

        {problem.type === 'short_answer' && (
          <div className="problem-short-answer">
            <h2></h2>
            <textarea
              value={shortAnswer}
              onChange={handleShortAnswerChange}
              placeholder="..."
              rows={8}
              className="short-answer-editor"
            />
          </div>
        )}
      </div>

      {/*  */}
      {problem.type === 'programming' ? (
        <div className="code-submission">
          <h2></h2>
          <form onSubmit={handleSubmit}>
            <div className="form-row">
              <label>:</label>
              <select
                value={form.language}
                onChange={handleLanguageChange}
                className="language-select"
              >
                <option value="python">Python</option>
                <option value="cpp">C++</option>
                <option value="java">Java</option>
                <option value="javascript">JavaScript</option>
              </select>
            </div>
            
            <div className="form-row">
              <label>:</label>
              <textarea
                value={form.code}
                onChange={handleCodeChange}
                placeholder="..."
                rows={20}
                className="code-editor"
              />
            </div>
            
            <div className="form-actions">
              <button 
                type="submit" 
                className="submit-btn" 
                disabled={submitting}
              >
                {submitting ? '...' : ''}
              </button>
            </div>
          </form>
        </div>
      ) : (
        <div className="answer-submission">
          <h2></h2>
          <form onSubmit={handleSubmit}>
            <div className="form-actions">
              <button 
                type="submit" 
                className="submit-btn" 
                disabled={submitting}
              >
                {submitting ? '...' : ''}
              </button>
            </div>
          </form>
        </div>
      )}

      {submission && (
        <div className="submission-result">
          <h3></h3>
          <p>ID: {submission.submission_id}</p>
          <p>: {submission.status}</p>
          <p>: {submission.message}</p>
        </div>
      )}
    </div>
  );
};

export default ProblemDetailPage;
