import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { getSubmissions } from '../services/api';
import './SubmissionsPage.css';

const SubmissionsPage = () => {
  const [submissions, setSubmissions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [filterProblemTitle, setFilterProblemTitle] = useState('');  // 
  const [appliedFilter, setAppliedFilter] = useState('');  // 
  
  const navigate = useNavigate();

  // 
  const handleApplyFilter = () => {
    console.log(':', filterProblemTitle);  // 
    setAppliedFilter(filterProblemTitle);
    // currentPageuseEffect
  };

  // 
  const handleClearFilter = () => {
    console.log('');  // 
    setFilterProblemTitle('');
    setAppliedFilter('');
    setCurrentPage(1);  // 
  };

  // useEffect
  useEffect(() => {
    if (appliedFilter !== '') {
      setCurrentPage(1);  // 
    }
  }, [appliedFilter]);

  // useEffectAPI
  useEffect(() => {
    fetchSubmissions();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentPage, appliedFilter]);  // appliedFilter

  const fetchSubmissions = async () => {
    try {
      setLoading(true);
      console.log('API:', { currentPage, appliedFilter });  // 
      
      // API
      const response = await getSubmissions(currentPage, 20, null, appliedFilter);
      
      console.log('API:', response);  // 
      setSubmissions(response.submissions);
      setTotalPages(response.pages);
      setTotal(response.total);
      setError(null);
    } catch (err) {
      setError('');
      console.error(':', err);
    } finally {
      setLoading(false);
    }
  };

  const handleProblemClick = (problemId) => {
    navigate(`/problem/${problemId}`);
  };

  const handleSubmissionClick = (submissionId) => {
    navigate(`/result/${submissionId}`);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'accepted':
        return 'status-accepted';
      case 'wrong_answer':
        return 'status-wrong';
      case 'error':
        return 'status-error';
      case 'time_limit_exceeded':
        return 'status-timeout';
      case 'memory_limit_exceeded':
        return 'status-memory';
      case 'judging':
        return 'status-judging';
      case 'pending':
        return 'status-pending';
      default:
        return 'status-default';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'accepted':
        return '';
      case 'wrong_answer':
        return '';
      case 'error':
        return '';
      case 'time_limit_exceeded':
        return '';
      case 'memory_limit_exceeded':
        return '';
      case 'judging':
        return '';
      case 'pending':
        return '';
      default:
        return status;
    }
  };

  const getProblemTypeText = (type) => {
    switch (type) {
      case 'programming':
        return '';
      case 'choice':
        return '';
      case 'judge':
        return '';
      case 'short_answer':
        return '';
      default:
        return type || '';
    }
  };

  const formatTime = (seconds) => {
    if (!seconds) return '-';
    return `${(seconds * 1000).toFixed(0)}ms`;
  };

  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleString('zh-CN');
  };

  if (loading && submissions.length === 0) {
    return (
      <div className="submissions-page">
        <div className="submissions-header">
          <h1></h1>
        </div>
        <div className="loading">...</div>
      </div>
    );
  }

  return (
    <div className="submissions-page">
      <div className="submissions-header">
        <h1></h1>
        <div className="submissions-stats">
           {total} 
        </div>
      </div>

      <div className="submissions-filters">
        <div className="filter-group">
          <label htmlFor="problemFilter">:</label>
          <input
            id="problemFilter"
            type="text"
            value={filterProblemTitle}
            onChange={(e) => setFilterProblemTitle(e.target.value)}
            placeholder=""
          />
          <button 
            onClick={handleApplyFilter}
            className="apply-filter-btn"
          >
            
          </button>
          <button 
            onClick={handleClearFilter}
            className="clear-filter-btn"
          >
            
          </button>
        </div>
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      <div className="submissions-table-container">
        <table className="submissions-table">
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {submissions.map((submission) => (
              <tr key={submission.id}>
                <td>{submission.id}</td>
                <td>
                  <button
                    className="problem-link"
                    onClick={() => handleProblemClick(submission.problem_id)}
                  >
                    {submission.problem_title || `${submission.problem_id}`}
                  </button>
                </td>
                <td>{getProblemTypeText(submission.problem_type)}</td>
                <td>{submission.language}</td>
                <td>
                  <span className={`status-badge ${getStatusColor(submission.status)}`}>
                    {getStatusText(submission.status)}
                  </span>
                </td>
                <td>{formatTime(submission.execution_time)}</td>
                <td>{formatDate(submission.created_at)}</td>
                <td>
                  <button
                    className="view-result-btn"
                    onClick={() => handleSubmissionClick(submission.id)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {submissions.length === 0 && !loading && (
        <div className="no-submissions">
          {appliedFilter ? '' : ''}
        </div>
      )}

      {totalPages > 1 && (
        <div className="pagination">
          <button
            onClick={() => setCurrentPage(currentPage - 1)}
            disabled={currentPage === 1}
            className="page-btn"
          >
            
          </button>
          <span className="page-info">
             {currentPage}  {totalPages} 
          </span>
          <button
            onClick={() => setCurrentPage(currentPage + 1)}
            disabled={currentPage === totalPages}
            className="page-btn"
          >
            
          </button>
        </div>
      )}
    </div>
  );
};

export default SubmissionsPage;
