import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { getSchools, getMajors, getClasses } from '../services/api';
import './RegisterPage.css';

const RegisterPage = () => {
  const [form, setForm] = useState({
    // 
    username: '',
    password: '',
    confirmPassword: '',
    name: '',
    email: '',
    phone: '',
    role: 'student',
    school_id: '',
    major_id: '',
    class_id: ''
  });
  
  const [schools, setSchools] = useState([]);
  const [majors, setMajors] = useState([]);
  const [classes, setClasses] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    fetchSchools();
  }, []);

  const fetchSchools = async () => {
    try {
      const schoolsData = await getSchools();
      setSchools(schoolsData);
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleSchoolChange = async (schoolId) => {
    setForm(prev => {
      const newForm = { ...prev, school_id: schoolId, major_id: '', class_id: '' };
      return newForm;
    });
    
    setMajors([]);
    setClasses([]);
    
    if (schoolId) {
      try {
        const majorsData = await getMajors(schoolId);
        setMajors(majorsData);
      } catch (error) {
        console.error(':', error);
      }
    }
  };

  const handleMajorChange = async (majorId) => {
    setForm(prev => ({ ...prev, major_id: majorId, class_id: '' }));
    setClasses([]);
    
    if (majorId) {
      try {
        const classesData = await getClasses(majorId);
        setClasses(classesData);
      } catch (error) {
        console.error(':', error);
      }
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    // 
    setError('');
  };

  const handleInputChange = (field, value) => {
    setForm(prev => ({ ...prev, [field]: value }));
  };

  return (
    <div className="register-container">
      <div className="register-card">
        <h1></h1>
        
        {error && <div className="error-message"> {error}</div>}
        {success && <div className="success-message"> {success}</div>}
        
        <form onSubmit={handleSubmit}>
          <div className="info-message" style={{marginBottom: '12px'}}>
            
          </div>
          {/*  */}
          <div className="form-section">
            <h3></h3>
            
            <div className="form-group">
              <label> *</label>
              <input
                type="text"
                value={form.username}
                onChange={(e) => handleInputChange('username', e.target.value)}
                placeholder=""
                required
              />
            </div>

            <div className="form-group">
              <label> *</label>
              <input
                type="text"
                value={form.name}
                onChange={(e) => handleInputChange('name', e.target.value)}
                placeholder=""
                required
              />
            </div>

            <div className="form-row">
              <div className="form-group">
                <label> *</label>
                <input
                  type="password"
                  value={form.password}
                  onChange={(e) => handleInputChange('password', e.target.value)}
                  placeholder="6"
                  required
                />
              </div>

              <div className="form-group">
                <label> *</label>
                <input
                  type="password"
                  value={form.confirmPassword}
                  onChange={(e) => handleInputChange('confirmPassword', e.target.value)}
                  placeholder=""
                  required
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label></label>
                <input
                  type="email"
                  value={form.email}
                  onChange={(e) => handleInputChange('email', e.target.value)}
                  placeholder=""
                />
              </div>

              <div className="form-group">
                <label></label>
                <input
                  type="tel"
                  value={form.phone}
                  onChange={(e) => handleInputChange('phone', e.target.value)}
                  placeholder=""
                />
              </div>
            </div>

            <div className="form-group">
              <label> *</label>
              <select
                value={form.role}
                onChange={(e) => handleInputChange('role', e.target.value)}
                required
              >
                <option value="student"></option>
                <option value="teacher"></option>
              </select>
            </div>
          </div>

          {/*  */}
          {form.role === 'student' && (
            <div className="form-section">
              <h3></h3>
              
              <div className="form-group">
                <label></label>
                <select
                  value={form.school_id}
                  onChange={(e) => {
                    const newValue = e.target.value;
                    handleSchoolChange(newValue);
                  }}
                >
                  <option value=""></option>
                  {schools.map(school => (
                    <option key={school.id} value={school.id}>
                      {school.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="form-group">
                <label></label>
                <select
                  value={form.major_id}
                  onChange={(e) => handleMajorChange(e.target.value)}
                  disabled={!form.school_id}
                >
                  <option value=""></option>
                  {majors.map(major => (
                    <option key={major.id} value={major.id}>
                      {major.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="form-group">
                <label></label>
                <select
                  value={form.class_id}
                  onChange={(e) => handleInputChange('class_id', e.target.value)}
                  disabled={!form.major_id}
                >
                  <option value=""></option>
                  {classes.map(cls => (
                    <option key={cls.id} value={cls.id}>
                      {cls.name}
                    </option>
                  ))}
                </select>
              </div>
            </div>
          )}

          <div className="form-actions">
            <button 
              type="submit" 
              disabled={loading || success}
              className="register-btn"
            >
              {loading ? '...' : success ? '' : ''}
            </button>
          </div>
        </form>

        <div className="login-link">
          <Link to="/login"></Link>
        </div>
      </div>
    </div>
  );
};

export default RegisterPage;
