//
// ID API 
// pending judging 5 
// ID ID

import React, { useState, useEffect } from 'react';
import { getResult } from '../services/api';
import { useParams } from 'react-router-dom';

const ResultPage = () => {
  const { submissionId } = useParams();
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchResult = async () => {
      try {
        const data = await getResult(submissionId);
        setResult(data);
        
        // accepted
        if (data.status === 'accepted') {
          console.log('');
          // 
          localStorage.setItem('submissionStatusChanged', 'true');
          // storage
          window.dispatchEvent(new StorageEvent('storage', {
            key: 'submissionStatusChanged',
            newValue: 'true'
          }));
        }
        
        // 5
        if (data.status === 'pending' || data.status === 'judging') {
          setTimeout(fetchResult, 5000);
        }
      } catch (error) {
        console.error(':', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchResult();
  }, [submissionId]);

  if (loading) {
    return <div>...</div>;
  }

  return (
    <div>
      <h1></h1>
      <p>ID: {submissionId}</p>
      <p>ID: {result.problem_id}</p>
      <p>: {result.status}</p>
      <p>: {result.result || 'N/A'}</p>
      {result.execution_time && (
        <p>: {result.execution_time.toFixed(2)}</p>
      )}
      
      {['pending', 'judging'].includes(result.status) && (
        <div>...</div>
      )}
    </div>
  );
};

export default ResultPage;