import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './TestPage.css';

const TestPage = () => {
  const [selectedType, setSelectedType] = useState('programming');

  const testProblems = [
    {
      id: 1,
      title: 'Hello World',
      type: 'programming',
      description: '"Hello, World!"',
      test_cases: '',
      expected_output: 'Hello, World!',
      difficulty: 'easy',
      time_limit: 1000,
      memory_limit: 128,
      is_multiple_choice: false
    },
    {
      id: 2,
      title: '',
      type: 'choice',
      description: '',
      choice_options: 'Python\nJava\nC++\nJavaScript',
      expected_output: 'A,B,C,D',
      difficulty: 'easy',
      is_multiple_choice: false
    },
    {
      id: 3,
      title: '',
      type: 'judge',
      description: 'Python',
      expected_output: '',
      difficulty: 'easy',
      is_multiple_choice: false
    },
    {
      id: 4,
      title: '',
      type: 'short_answer',
      description: '',
      expected_output: ',,',
      difficulty: 'medium',
      is_multiple_choice: false
    },
    {
      id: 5,
      title: '',
      type: 'choice',
      description: '',
      choice_options: 'HTML\nCSS\nJavaScript\nPython\nReact',
      expected_output: 'A,B,C,E',
      difficulty: 'medium',
      is_multiple_choice: true
    }
  ];

  const getTypeText = (type) => {
    switch (type) {
      case 'programming': return '';
      case 'choice': return '';
      case 'judge': return '';
      case 'short_answer': return '';
      default: return '';
    }
  };

  const getDifficultyColor = (diff) => {
    switch (diff) {
      case 'easy': return '#52c41a';
      case 'medium': return '#faad14';
      case 'hard': return '#f5222d';
      default: return '#666';
    }
  };

  return (
    <div className="test-page">
      <div className="container">
        <h1></h1>
        <p></p>
        
        <div className="problem-type-selector">
          <h3></h3>
          <div className="type-buttons">
            {['programming', 'choice', 'judge', 'short_answer'].map(type => (
              <button
                key={type}
                className={`type-btn ${selectedType === type ? 'active' : ''}`}
                onClick={() => setSelectedType(type)}
              >
                {getTypeText(type)}
              </button>
            ))}
          </div>
        </div>

        <div className="test-problems">
          <h3></h3>
          <div className="problems-grid">
            {testProblems
              .filter(problem => selectedType === 'all' || problem.type === selectedType)
              .map(problem => (
                <div key={problem.id} className="problem-card">
                  <div className="problem-header">
                    <h4>{problem.title}</h4>
                    <div className="problem-badges">
                      <span 
                        className="type-badge"
                        style={{ backgroundColor: '#1890ff' }}
                      >
                        {getTypeText(problem.type)}
                      </span>
                      <span 
                        className="difficulty-badge"
                        style={{ backgroundColor: getDifficultyColor(problem.difficulty) }}
                      >
                        {problem.difficulty === 'easy' ? '' : 
                         problem.difficulty === 'medium' ? '' : ''}
                      </span>
                    </div>
                  </div>
                  
                  <div className="problem-description">
                    {problem.description}
                  </div>
                  
                  {problem.type === 'programming' && (
                    <div className="problem-meta">
                      <span>: {problem.time_limit}ms</span>
                      <span>: {problem.memory_limit}MB</span>
                    </div>
                  )}
                  
                  <div className="problem-actions">
                    <Link 
                      to={`/problem/${problem.id}`} 
                      className="view-btn"
                      state={{ problem: problem }}
                    >
                      
                    </Link>
                  </div>
                </div>
              ))}
          </div>
        </div>

        <div className="test-instructions">
          <h3></h3>
          <ul>
            <li><strong></strong></li>
            <li><strong></strong></li>
            <li><strong></strong>""""</li>
            <li><strong></strong></li>
          </ul>
          
          <div className="note">
            <strong></strong> 
            
          </div>
        </div>
      </div>
    </div>
  );
};

export default TestPage;
