import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { registerAdmin } from '../services/api';
import './AdminRegisterPage.css';

const AdminRegisterPage = () => {
  const [form, setForm] = useState({
    username: '',
    name: '',
    password: '',
    confirmPassword: '',
    email: '',
    phone: '',
    school_name: '',
    major_name: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setSuccess('');

    // 
    if (form.password !== form.confirmPassword) {
      setError('');
      setLoading(false);
      return;
    }

    if (form.password.length < 6) {
      setError('6');
      setLoading(false);
      return;
    }

    try {
      await registerAdmin({
        username: form.username,
        name: form.name,
        password: form.password,
        email: form.email || undefined,
        phone: form.phone || undefined,
        school_name: form.school_name || undefined,
        major_name: form.major_name || undefined
      });

      setSuccess('...');
      
      // 3
      setTimeout(() => {
        navigate('/login');
      }, 3000);

    } catch (error) {
      setError(error.response?.data?.error || '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="admin-register-container">
      <div className="admin-register-card">
        <h1> </h1>
        
        {error && <div className="error-message">{error}</div>}
        {success && <div className="success-message">{success}</div>}
        
        <form onSubmit={handleSubmit}>
          <div className="form-row">
            <div className="form-group">
              <label><span className="required-mark">*</span></label>
              <input
                type="text"
                value={form.username}
                onChange={(e) => setForm({...form, username: e.target.value})}
                required
                placeholder=""
                autoComplete="username"
              />
            </div>
            
            <div className="form-group">
              <label><span className="required-mark">*</span></label>
              <input
                type="text"
                value={form.name}
                onChange={(e) => setForm({...form, name: e.target.value})}
                required
                placeholder=""
                autoComplete="name"
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label><span className="required-mark">*</span></label>
              <input
                type="password"
                value={form.password}
                onChange={(e) => setForm({...form, password: e.target.value})}
                required
                placeholder="6"
                autoComplete="new-password"
                minLength="6"
              />
            </div>
            
            <div className="form-group">
              <label><span className="required-mark">*</span></label>
              <input
                type="password"
                value={form.confirmPassword}
                onChange={(e) => setForm({...form, confirmPassword: e.target.value})}
                required
                placeholder=""
                autoComplete="new-password"
                minLength="6"
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label></label>
              <input
                type="email"
                value={form.email}
                onChange={(e) => setForm({...form, email: e.target.value})}
                placeholder=""
                autoComplete="email"
              />
            </div>
            
            <div className="form-group">
              <label></label>
              <input
                type="tel"
                value={form.phone}
                onChange={(e) => setForm({...form, phone: e.target.value})}
                placeholder=""
                autoComplete="tel"
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label></label>
              <input
                type="text"
                value={form.school_name}
                onChange={(e) => setForm({...form, school_name: e.target.value})}
                placeholder=""
              />
            </div>
            
            <div className="form-group">
              <label></label>
              <input
                type="text"
                value={form.major_name}
                onChange={(e) => setForm({...form, major_name: e.target.value})}
                placeholder=""
              />
            </div>
          </div>
          
          <div className="form-actions">
            <button type="submit" className="register-btn" disabled={loading}>
              {loading ? '...' : ''}
            </button>
            
            <button 
              type="button" 
              className="back-btn"
              onClick={() => navigate('/login')}
            >
              
            </button>
          </div>
        </form>
        
        <div className="register-info">
          <div className="info-box">
            <p> </p>
            <ul>
              <li></li>
              <li>6</li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminRegisterPage;
