import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { submitCode } from '../services/api';
import './SubmitPage.css';

const SubmitPage = () => {
  const { problemId } = useParams();
  const [form, setForm] = useState({
    problem_id: '',
    code: '',
    language: 'python'
  });
  const [submission, setSubmission] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (problemId) {
      setForm(prev => ({ ...prev, problem_id: Number(problemId) }));
    }
  }, [problemId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    
    try {
      const payload = { ...form, problem_id: Number(form.problem_id) };
      const result = await submitCode(payload);
      setSubmission(result);
      console.log(':', result);
    } catch (error) {
      console.error(':', error);
      setError(error.response?.data?.error || '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="submit-container">
      <h1></h1>
      
      {error && (
        <div className="error-message">
           {error}
        </div>
      )}
      
      {submission ? (
        <div className="submission-success">
          <h2> </h2>
          <div className="submission-info">
            <p><strong>ID:</strong> {submission.submission_id}</p>
            <p><strong>:</strong> <span className="status-pending">{submission.status}</span></p>
            <p><strong>:</strong> {submission.message}</p>
          </div>
          <div className="submission-actions">
            <a href={`/result/${submission.submission_id}`} className="view-result-btn">
              
            </a>
            <button 
              onClick={() => {
                setSubmission(null);
                setForm({ problem_id: problemId ? Number(problemId) : '', code: '', language: 'python' });
                setError('');
              }}
              className="submit-again-btn"
            >
              
            </button>
          </div>
        </div>
      ) : (
        <form onSubmit={handleSubmit} className="submit-form">
          {problemId ? (
            <div className="form-group readonly-field">
              <label>ID:</label>
              <div className="readonly-value">{form.problem_id}</div>
            </div>
          ) : (
            <div className="form-group">
              <label>ID:</label>
              <input 
                type="number" 
                value={form.problem_id}
                onChange={(e) => setForm({...form, problem_id: e.target.value})}
                placeholder="ID"
                required
                min="1"
              />
            </div>
          )}
          
          <div className="form-group">
            <label>:</label>
            <select
              value={form.language}
              onChange={(e) => setForm({...form, language: e.target.value})}
              className="language-select"
            >
              <option value="python">Python</option>
              <option value="cpp">C++</option>
              <option value="java">Java</option>
              <option value="javascript">JavaScript</option>
            </select>
          </div>
          
          <div className="form-group">
            <label>:</label>
            <textarea
              rows={15}
              value={form.code}
              onChange={(e) => setForm({...form, code: e.target.value})}
              placeholder="..."
              required
              className="code-textarea"
            />
          </div>
          
          <button type="submit" disabled={loading} className="submit-btn">
            {loading ? '...' : ''}
          </button>
        </form>
      )}
    </div>
  );
};

export default SubmitPage;