import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { login } from '../services/api';
import './LoginPage.css';

const LoginPage = () => {
  const [loginType, setLoginType] = useState('student'); // 'student', 'teacher', 'admin'
  const [form, setForm] = useState({
    username: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    
    try {
      const result = await login(form);
      localStorage.setItem('token', result.token);
      localStorage.setItem('user', JSON.stringify(result.user));
      // 
      window.dispatchEvent(new Event('auth-changed'));
      
      // 
      if (result.user.role === 'admin') {
        navigate('/admin/dashboard');
      } else if (result.user.role === 'teacher') {
        navigate('/teacher/dashboard');
      } else {
        navigate('/problems');
      }
    } catch (error) {
      setError(error.response?.data?.error || '');
    } finally {
      setLoading(false);
    }
  };

  const handleLoginTypeChange = (type) => {
    setLoginType(type);
    setForm({ username: '', password: '' });
    setError('');
  };

  const getPlaceholder = () => {
    if (loginType === 'student') return '';
    if (loginType === 'teacher') return '';
    return '';
  };

  const getLabel = () => {
    if (loginType === 'student') return '';
    if (loginType === 'teacher') return '';
    return '';
  };

  return (
    <div className="login-container">
      {/*  */}
      <div className="bg-decoration">
        <div className="decoration-circle circle-1"></div>
        <div className="decoration-circle circle-2"></div>
        <div className="decoration-circle circle-3"></div>
      </div>
      
      <div className="login-card">
        {/*  */}
        <div className="login-header">
          <div className="logo-section">
            <div className="logo-icon"></div>
            <h1>OJ</h1>
            <p className="subtitle">Online Judge System</p>
          </div>
        </div>
        
        {/*  */}
        <div className="login-type-selector">
          <button
            type="button"
            className={`type-btn ${loginType === 'student' ? 'active' : ''}`}
            onClick={() => handleLoginTypeChange('student')}
          >
            <span className="type-icon"></span>
            <span className="type-text"></span>
          </button>
          <button
            type="button"
            className={`type-btn ${loginType === 'teacher' ? 'active' : ''}`}
            onClick={() => handleLoginTypeChange('teacher')}
          >
            <span className="type-icon"></span>
            <span className="type-text"></span>
          </button>
          <button
            type="button"
            className={`type-btn ${loginType === 'admin' ? 'active' : ''}`}
            onClick={() => handleLoginTypeChange('admin')}
          >
            <span className="type-icon"></span>
            <span className="type-text"></span>
          </button>
        </div>

        {error && <div className="error-message">{error}</div>}
        
        {/*  */}
        <div className="form-section">
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label>
                {getLabel()}
                <span className="input-icon"></span>
              </label>
              <div className="input-wrapper">
                <input
                  type="text"
                  value={form.username}
                  onChange={(e) => setForm({...form, username: e.target.value})}
                  required
                  placeholder={getPlaceholder()}
                  autoComplete="username"
                />
              </div>
            </div>
            
            <div className="form-group">
              <label>
                
                <span className="input-icon"></span>
              </label>
              <div className="input-wrapper">
                <input
                  type="password"
                  value={form.password}
                  onChange={(e) => setForm({...form, password: e.target.value})}
                  required
                  placeholder=""
                  autoComplete="current-password"
                />
              </div>
            </div>
            
            <button type="submit" className="login-btn" disabled={loading}>
              {loading ? (
                <span className="loading-text">
                  <span className="loading-spinner"></span>
                  ...
                </span>
              ) : (
                <span> </span>
              )}
            </button>
          </form>
        </div>
        
        {/*  */}
        <div className="login-info">
          {loginType === 'student' ? (
            <div className="info-box">
              <div className="info-header">
                <span className="info-icon"></span>
                <span className="info-title"></span>
              </div>
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          ) : loginType === 'teacher' ? (
            <div className="info-box">
              <div className="info-header">
                <span className="info-icon"></span>
                <span className="info-title"></span>
              </div>
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          ) : (
            <div className="info-box">
              <div className="info-header">
                <span className="info-icon"></span>
                <span className="info-title"></span>
              </div>
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          )}
        </div>
        
        {/*  */}
        <div className="login-links">
          <a href="/forgot-password"> </a>
          {loginType === 'admin' && <a href="/admin/register"> </a>}
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
